/* Footer Main Container */
.footer {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: #000000; /* Black background */
  color: #ffffff; /* White text */
  padding: 40px 20px;
  font-family: "Poppins", Arial, sans-serif;
}

/* Footer Inner Container */
.footer-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 40px;
  max-width: 1200px;
  margin: 0 auto;
}

/* Individual Footer Section */
.footer-section {
  flex: 1;
  min-width: 200px;
}

/* Section Titles */
.footer-section h3 {
  font-size: 1.3rem;
  margin-bottom: 15px;
  font-weight: 600;
  color: #5c5c5c; /* Grey heading color */
}

/* Section Content */
.footer-section p {
  color: white;
  font-size: 0.9rem;
  line-height: 1.6;
  margin-bottom: 10px;
}

/* Quick Links and Popular Services */
.footer-section ul {
  list-style: none;
  padding: 0;
}

.footer-section ul li {
  margin-bottom: 10px;
}

.footer-section a {
  color: #706d5f; /* Grey links */
  text-decoration: none;
  transition: color 0.3s;
}

.footer-section a:hover {
  color: #fff; /* White on hover */
}

/* About Section Link */
.more-link {
  display: inline-block;
  margin-top: 10px;
  color: #ffcc00;
  text-decoration: none;
  font-weight: 500;
  transition: transform 0.3s;
}

.more-link:hover {
  transform: translateX(5px); /* Small animation */
  color: #fff;
}

/* Footer Bottom Section */
.footer-bottom {
  margin-top: 20px;
  text-align: center;
  border-top: 1px solid #444;
  padding-top: 15px;
}
.footer-bottom p{
  color: white;
}
.footer-bottom span {
  color: #909090;
  font-weight: 800;
}

/* Responsive Adjustments */

/* Medium Screens (max-width: 768px) */
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column; /* Stack sections vertically */
    gap: 30px;
  }

  .footer-section {
    text-align: center; /* Center-align text for better presentation */
    min-width: unset; /* Remove minimum width constraint */
  }
}

/* Small Screens (max-width: 480px) */
@media (max-width: 480px) {
  .footer {
    padding: 20px 10px; /* Reduce padding for smaller screens */
  }

  .footer-section h3 {
    font-size: 1.2rem; /* Slightly smaller titles */
  }

  .footer-section p,
  .footer-section ul li {
    font-size: 0.85rem; /* Adjust font size for readability */
  }

  .footer-bottom {
    font-size: 0.8rem; /* Adjust footer bottom text size */
  }
}
